import API from '@/utils/api.js'

export default {
  state: {
    managerList: [],
    managerDetail: {}
  },
  mutations: {
    SET_MANAGER_LIST: (state, payload) => {
      state.managerList = [...payload]
    },
    SET_MANAGER_DETAIL: (state, payload) => {
      state.managerDetail = Object.assign({}, payload)
    }
  },
  actions: {
    getManagerList: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/list`,
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_MANAGER_LIST', data.data)
          resolve({
            list: data.data,
            total: data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    addManager: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/add`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getManagerDetail: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/${payload}`,
          method: 'GET'
        }).then(res => {
          const data = res.data
          commit('SET_MANAGER_DETAIL', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    deleteManager: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/${payload}`,
          method: 'DELETE'
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    updateManager: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/${payload.id}`,
          method: 'PUT',
          data: payload.data
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    ban: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/enable_batch`,
          method: 'PUT',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    }
  },
  namespaced: true
};
