import API from '@/utils/api.js'

export default {
  state: {
    readerList: [],
    readerDetail: {},
    needExamineReader: [],
    recordList: []
  },
  mutations: {
    SET_READER_LIST: (state, payload) => {
      state.readerList = [...payload]
    },
    SET_READER_DETAIL: (state, payload) => {
      state.readerDetail = Object.assign({}, payload)
    },
    SET_NEED_EXAMINER_READER: (state, payload) => {
      state.needExamineReader = Object.assign({}, payload)
    },
    SET_RECORD_LIST: (state, payload) => {
      state.recordList = [...payload]
    }
  },
  actions: {
    getReaderList: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user/list`,
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_READER_LIST', data.data)
          resolve({
            list: data.data,
            total: data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    getNeedExamineReaderList: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user_apply/list?${payload.status}`,
          method: 'GET',
          params: payload.params
        }).then(res => {
          const data = res.data
          commit('SET_NEED_EXAMINER_READER', data.data)
          resolve({
            list: data.data,
            total: data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    readerVertify: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user_apply/verify`,
          method: 'PUT',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getReaderDetail: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user/${payload}`,
          method: 'GET'
        }).then(res => {
          const data = res.data
          commit('SET_READER_DETAIL', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    switchReaderStatus: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user/enable_batch`,
          method: 'PUT',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getReaderRecord: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/borrow_record/list`,
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_RECORD_LIST', data.data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    ban: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/user/enable_batch`,
          method: 'PUT',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    }
  },
  namespaced: true
};
