const menu = [
  {
    path: 'home',
    name: 'Home',
    component: () => import('@/views/home/index.vue'),
    meta: {
      title: '首页',
      icon: 'bar-chart',
      auth: true
    }
  },
  {
    path: 'book',
    name: 'Book',
    component: () =>import('@/views/book/index.vue'),
    meta: {
      title: '图书管理',
      icon: 'book',
      auth: true
    },
    children: [
      {
        path: 'manage',
        name: 'Book Manage',
        redirect: 'manage',
        component: () => import('@/views/book/manage/index.vue'),
        meta: {
          title: '书籍管理'
        },
        children: [
          {
            path: '/',
            component: () => import('@/views/book/manage/Manage.vue')
          },
          {
            path: 'edit/:id',
            name: 'Book Detail',
            component: () => import('@/views/book/manage/Detail.vue')
          },
          {
            path: 'create',
            name: 'Book Create',
            component: () => import('@/views/book/manage/Create.vue')
          }
        ]
      },
      {
        path: 'class',
        name: 'Class Manage',
        component: () => import('@/views/book/Class.vue'),
        meta: {
          title: '分类管理'
        }
      }
    ]
  },
  {
    path: 'person',
    name: 'person',
    component: () =>import('@/views/person/index.vue'),
    meta: {
      title: '人员管理',
      icon: 'radar-chart',
      auth: true
    },
    children: [
      {
        path: 'reader',
        name: 'Reader',
        redirect: 'reader',
        component: () => import('@/views/person/reader/index.vue'),
        meta: {
          title: '读者管理'
        },
        children: [
          {
            path: '',
            name: 'Reader Manage',
            component: () => import('@/views/person/reader/Reader.vue'),
          },
          {
            path: 'examine',
            name: 'Reader Examine',
            component: () => import('@/views/person/reader/Examine.vue')
          }
        ]
      },
      {
        path: 'manage',
        name: 'Manager',
        redirect: 'manage',
        component: () => import('@/views/person/manager/index.vue'),
        meta: {
          title: '管理员管理',
          has_rights: true
        },
        children: [
          {
            path: '',
            name: 'Manage Manager',
            component: () => import('@/views/person/manager/Manage.vue'),
          },
          {
            path: 'record',
            name: 'Manager Record',
            component: () => import('@/views/person/manager/Record.vue')
          }
        ]
      }
    ]
  },
  {
    path: 'setting',
    name: 'Setting',
    component: () => import('@/views/Setting.vue'),
    meta: {
      title: '个人中心',
      icon: 'setting',
      auth: true
    }
  }
]

export default menu