import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import * as echarts from 'echarts'
import Plugins from '@/plugins/index.js'

import '@/assets/scss/normalize.scss'
import '@/assets/scss/common.scss'

import { 
  Button,
  Input,
  Table,
  Row,
  Col,
  Form,
  Breadcrumb,
  Select,
  Modal,
  Radio,
  Upload,
  Icon,
  Message,
  Spin,
  FormModel,
  Avatar,
} from 'ant-design-vue'

Vue.use(Button)
   .use(Input)
   .use(Table)
   .use(Row)
   .use(Col)
   .use(Form)
   .use(Breadcrumb)
   .use(Select)
   .use(Modal)
   .use(Radio)
   .use(Upload)
   .use(Icon)
   .use(Spin)
   .use(FormModel)
   .use(Avatar)
Vue.config.productionTip = false
Vue.prototype.$echarts = echarts
Vue.prototype.$message = Message
Vue.prototype.$confirm = Modal.confirm

Vue.use(Plugins)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
