import API from '@/utils/api.js'

export default {
  state: {
    categoryList: [],
    allCategoryList: {},
    booksList: [],
    bookDetail: {},
    categoryDetail: {}
  },
  mutations: {
    SET_CATEGORY_LIST: (state, payload) => {
      state.categoryList = [...payload]
    },
    SET_BOOKS_LIST: (state, payload) => {
      state.booksList = [...payload]
    },
    SET_BOOKS_Detail: (state, payload) => {
      state.bookDetail = Object.assign({}, payload)
    },
    SET_ALL_CATEGORY_LIST: (state, payload) => {
      state.allCategoryList = Object.assign({}, payload)
    },
    SET_CATEGORY_Detail: (state, payload) => {
      state.categoryDetail = Object.assign({}, payload)
    }
  },
  actions: {
    getBooks: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: '/book/list',
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_BOOKS_LIST', data.data)
          resolve({
            list: data.data,
            total: data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    getBookDetail: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/${payload}`,
          method: 'GET'
        }).then(res => {
          const data = res.data
          commit('SET_BOOKS_Detail', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    addBooks: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/add`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    upadateBooks: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/${payload.id}`,
          method: 'PUT',
          data: payload.data
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    deleteBook: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/${payload}`,
          method: 'DELETE',
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    offbook: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/offshelf/${payload.id}`,
          method: 'PUT',
          data: payload.data
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getBookCategory: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: '/category/list',
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_CATEGORY_LIST', data.data)
          resolve({
            list: data.data,
            total: data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    getAllBookCategory:({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: '/category/all',
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_ALL_CATEGORY_LIST', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    addCategory: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: '/category/add',
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    updateCategory: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/category/${payload.id}`,
          method: 'PUT',
          data: payload.data
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    deleteCategory: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/category/${payload}`,
          method: 'DELETE',
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getCategoryDetail: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/category/${payload}`,
          method: 'GET'
        }).then(res => {
          const data = res.data
          commit('SET_CATEGORY_Detail', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    importBooks: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/import`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    }
  },
  namespaced: true
};
