const download = (res) => {
    console.log(typeof res.data)
    let years = new Date().getFullYear();
    let month = new Date().getMonth()+1>9?new Date().getMonth() + 1 : "0" + (new Date().getMonth()+1);
    let date = new Date().getDate()+1>9?new Date().getDate() : "0" + new Date().getDate();
    let dataString = `${years}-${month}-${date}`
    let url = window.URL.createObjectURL(res.data)
    let link = document.createElement('a')
    let filename = res.headers['content-disposition'].split('=')[1].replace(/"/g, '')
    // let filename = "广州市房地产行业委员会会员列表"
    //修改master report bug 而修改filename规则，后端若改变响应头则用回上面注释
    //let filename = res.headers['content-disposition'].split('=')[1].replace(/"/g, '') == dataString + '_Bookings Log.xlsx'?`${dataString}_Account & Contact_Profile_Master_Report`:res.headers['content-disposition'].split('=')[1].replace(/"/g, '')
    link.download = filename
    link.style.display = 'none'
    link.href = url
    document.body.appendChild(link)
    link.click()
    window.URL.revokeObjectURL(url)
    document.body.removeChild(link)
}

export default {
    install(Vue) {
        Vue.prototype.download = download
    },
}
